.element__pic {
  width: 100%;
  height: 282px;
  display: block;
  object-fit: cover;
  object-position: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  cursor: pointer;
  transition: all 1s ease;
}

.element__pic:hover {
  opacity: .95;
  transform: scale(1.01);
}